.planet{
    position: relative;
  
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    
    flex: 1 1 0;
    width: 0;
  
    max-width: 40vw;
  
    margin: 0 5%;
  
    border-radius: 50%;
  
    animation: travel 2s ease-in-out infinite alternate-reverse;
    -webkit-animation: travel 2s ease-in-out infinite alternate-reverse;
    -moz-animation: travel 2s ease-in-out infinite alternate-reverse;
  
    transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
  }
  
  .planet:hover{
    scale: 110%;
  }
  
  .planet img{
    display: block;
    width: 100%;
  }

  .underplanet{
    position: absolute;
    width: 100%;
    height: 100%;
    
    border-radius: 50%;
  
    padding: 15%;
  
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }